<template>
    <div class="container-fluid">
        <div class="row text-center align-items-center justify-content-center">
            <div class="col-12 text-center py-3 mt-5">
                <h1 class="my-3">{{prop}}</h1>
            </div>
             <div class="col-12 col-md-6 form-outline theme-panel text-center mb-5 mx-3">
                <input type="search" class="form-control theme-text" @keypress.enter="get_result()" v-model="search" />
            </div>
        </div>
        <div v-if="watchData.length > 0" class="d-flex row align-items-center justify-content-center">
            <AppCart v-for="watch in watchData" :key="watch.filmId" :state="'default'" :type="watch.type" :kpid="watch.filmId" :title="watch.nameRu" :year="watch.year" />
        </div>
        <div v-else class="row text-center">
             <div class="col-12 text-center py-3">
                <h3 class="my-3">{{search_state}}</h3>
            </div>
        </div>
    </div>
</template>

<script>
import AppCart from '../components/AppCart.vue'
const axios = require('axios')
export default {
    name: 'Search',
    components: {
        AppCart

    },
    data: () => {
        return {
            prop: 'Поиск',
            search_state: 'Начните искать',
            search: 'Search',
            watchData: []
        }
    },
    methods: {
        get_result() {
            axios
                .get('https://iny.su/api.php?_action=media.search&v=0.1&query=' +encodeURIComponent(this.search))
                .then(data => {
                    if (data.data.code == 200) {
                        this.watchData = data.data.content
                    }
                    if (data.data.code == 404) {
                        this.watchData = []
                        this.search_state = 'Ничего не найдено'
                    }
                })
        }
    },
    mounted() {
        document.title = 'Search'
    }
}
</script>